declare var swal: any;
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { errorMessage } from 'src/app/messages';
import { DataService } from 'src/app/service/data.service';
import { PaginationService } from 'src/app/service/pagination.service';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { CommonFunction } from 'src/app/common';
import { ISubscription } from "rxjs/Subscription";
import { SubscribeService } from 'src/app/service/subscribe.service';

@Component({
  selector: 'app-subscribe-list',
  templateUrl: './subscribe-list.component.html'
})
export class SubscribeListComponent implements OnInit {

  // listing
  loadingState = true;
  objectArray: Array<any> = [];
  questionArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  private subscription: ISubscription;

  // form
  addForm: FormGroup;
  formErrors = {
    apierror: null
  };
  showLoader = false;
  showAddForm = false; // add
  isEditing = false; // edit
  editId: string = null; // edit
  editIndex: string = null; // edit
  loadform = false;
  permissionObject: any = null;

  constructor(
    private router: Router,
    private paginationService: PaginationService,
    private subscribeservice: SubscribeService,
    private dataService: DataService,
    private fBuilder: FormBuilder,
  ) {
    this.addForm = this.fBuilder.group({
      'email': [null, Validators.compose([Validators.required,Validators.email])],
    });
  }

  ngOnInit() {
    this.subscription= this.dataService.searchTextSubject.subscribe((v) =>{ 
      this.searchText=v;
      this.getObjects();
    });
  }



  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.subscribeservice.getSubscribeList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.data;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }



  deleteObject(object) {
    swal.fire({
      title: errorMessage.delete_header_text,
      text: errorMessage.delete_smalll_text,
      icon: errorMessage.delete_dialogue_type,
      showCancelButton: true,
      confirmButtonText: errorMessage.delete_confirm_button,
      cancelButtonText: errorMessage.delete_cancel_button,
    }).then((result) => {
      if (result.value) {
        this.subscribeservice.deleteSubscribe(object.id).subscribe(
          (response) => {
            if (response.success) {
              this.getObjects();
            }
          },
        );
      }
    }).catch(swal.noop);
  }

  editObject(object, index) {
    this.resetForm();
    this.editIndex = index;
    this.isEditing = true;
    this.editId = object.id;
    this.addForm.patchValue({
      'email': object.email
    });
  }

 

  showForm() {
    this.resetForm();
    this.showAddForm = true;
  }

  resetForm() {
    this.showAddForm = false;
    this.isEditing = false;
    this.editId = null;
    CommonFunction.resetForm(this.addForm, this.formErrors);
  }


  submitForm(): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      const formData = {
        'email': this.addForm.value.email
      };
      if (this.isEditing) {

        this.subscribeservice.editSubscribe(this.editId, formData).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.getObjects();
            this.resetForm();
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('email')) {
                this.formErrors['email'] = obj['email'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          console.log('In error', error);
          this.formErrors['apierror'] = `* ${error.error}`;
          this.showLoader = false;
        });
      } else {
        this.subscribeservice.addSubscribe(formData).subscribe((response) => {
          // console.log('In success', response);
          this.showLoader = false;
          if (response.success) {
            this.resetForm();
            this.getObjects();
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('email')) {
                this.formErrors['email'] = obj['email'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          console.log('In error', error);
          this.formErrors['apierror'] = `* ${error.error}`;
          this.showLoader = false;
        });
      }
    }
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
